<template>
  <div>
    <v-dialog
      transition="dialog-top-transition"
      max-width="600"
      v-model="carga_Activar"
      persistent
    >
      <template>
        <v-card max-width="600" dark>
          <v-card-text>
            <v-container >
              <v-row>
                <v-col cols="12" class="text-center">
                  <p>
                    {{ Informacion }}
                    
                  </p>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" v-if="Esperar" class="text-center">
                  <v-progress-circular
                    :size="100"
                    color="primary"
                    indeterminate
                  >Cargando</v-progress-circular>
                </v-col>
                <v-col cols="12" v-if="!Esperar" class="text-center">
                  <v-progress-circular
                    :size="100"
                    color="green"                    
                    value=100
                  v-if="Correcto">Finalizado</v-progress-circular>
                  <v-progress-circular
                    :size="100"
                    color="red"                    
                    value=100
                    v-if="!Correcto"
                  >Error</v-progress-circular>
                  
                </v-col>
                <v-col cols="12">
                  <p class="text-center text-capitalize">{{Datos}}</p>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions v-if="Boton">
              <v-col cols="12" class="text-center">
                  <v-btn rounded outlined @click="botone()"> Cerrar</v-btn>
              </v-col>
            
          </v-card-actions>
        </v-card>
      </template>
    </v-dialog>
  </div>
</template>
<script>
import {mapState} from 'vuex'
import { mapMutations } from 'vuex'
export default {
  name: "Carga",  
  computed:{
      ...mapState(['carga_Activar','Informacion','Esperar','Correcto','Datos','Boton','ruta']),
  },methods: {
    ...mapMutations(['Modificar_cargar','Reiniciar_Valores']),
    botone(){

      if(this.ruta != null && this.Correcto){

        this.$router.push(this.ruta);  
      } 
      this.Reiniciar_Valores()       
    }
  },
};
</script>
