<template>
<v-card color="rgba(0,0,0,0.8)" min-height="700">
    <v-card-text>
       <v-container class="px-0 white--text">
    <v-row justify="center" class="rounded-lg" color="primary">
      <v-col cols="12" order="0" v-if="titulo">
        <p class="text-h4 text-center font-weight-black">
          Cambio de Contraseña
        </p>
      </v-col>
      <v-col cols="12">
        <p>
          Una contraseña segura contribuye a evitar el acceso no autorizado a
          una cuenta.
        </p>
      </v-col>
      <v-col cols="12" md="7" v-if="titulo">
        <v-text-field
          v-model="password"
          :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
          :rules="[rules.required, rules.min]"
          :type="show1 ? 'text' : 'password'"
          name="input-1"
          label="Contraseña Actual"
          color="primary"
          counter
          @click:append="show1 = !show1"          
          filled
          rounded
          dense
          dark
        >
        </v-text-field>
      </v-col>
      <v-col cols="12" md="7">
        <span
          >La contraseña debe de ser de minimo 8 caracteres y debe tener al
          menos:
              <p v-if="!letras_m">                
                  Una letra minuscula [a-z]
                </p>

              <p v-if="!letras_M">                
                
                 Una letra mayuscula [A-Z]
              </p>  
              <p v-if="!numero">                
                
                  Un numero [0-9]
                
              </p>
              <p v-if="!raro">                
                Un caracter raro [$@$!%*?& _]
                
              </p>
            
          
          
        </span>
        <v-divider></v-divider>
        <v-text-field
          v-model="passwordn"
          :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
          :rules="[rules.required, rules.min, rules.formato]"
          :type="show2 ? 'text' : 'password'"
          name="input-2"
          label="Nueva Contraseña"
          hint="Minimo 8 caracteres incluido un caracter raro"
          counter
          @click:append="show2 = !show2"
          
          filled
          rounded
          dense
          dark
        >
        </v-text-field>
      </v-col>
      <v-col cols="12" md="7" v-if="completo">
        <v-text-field
          v-model="passwordnc"
          :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
          :rules="[rules.required, rules.min]"
          :type="show3 ? 'text' : 'password'"
          name="input-3"
          label="Repetir Nueva Contraseña"
          counter
          @click:append="show3 = !show3"
          
          filled
          rounded
          dense
          dark          
        >
        </v-text-field>
        <v-divider v-if="!iguales"></v-divider>
        <p v-if="!iguales">Las contraseñas no son iguales</p>
      </v-col>
      <v-col
        cols="12"
        md="7"
        align-self="center"
        class="text-center"
        v-if="terminar"
      >
        <v-btn elevation="2" outlined rounded  color="light-green accent-3" @click="Asistencia_Docente()">
          Cambio de Contraseña
        </v-btn>
      </v-col>
      <Carga
      :Info="Info"
      :Espera="Espera"
      :Correcto="Correcto"
      :Datos="information"
      :Boton="error?error:true"
      
    />
    </v-row>
  </v-container>     
       

    </v-card-text>


  </v-card>
 
</template>
<script>
import Carga from "../components/Carga.vue";
import axios from "axios";
import { mapMutations , mapState} from "vuex";
export default {
  name: "Home",
  components: { Carga },
  props:{
    titulo: {
      type: Boolean,
      default: true
    }
  },
  data: () => ({
    show1: false,
    show2: false,
    show3: false,
    password: "",
    passwordn: "",
    passwordnc: "",
    rules: {
      required: (value) => !!value || "Se requiere",
      min: (value) => value.length >= 8 || "Minimo 8 caracteres",
      formato: (value) => {
        const pattern = /(?=.*[a-z])+(?=.*[A-Z])+(?=.*\d)+(?=.*[$@$!%*?& _])+/;
        return pattern.test(value) || "Contraseña no valida";
      },
    },
    //DATOS
      Info: "",
      Espera: true,
      Correcto: false,
      information:"",
      error:false
  }),

  computed: {
    letras_m(){
      let minus= /(?=.*[a-z])/;
      return !!this.passwordn && minus.test(this.passwordn);
    },
    letras_M(){
      let mayus= /(?=.*[A-Z])/;
      return !!this.passwordn && mayus.test(this.passwordn);
    },
    numero(){
      let nume= /(?=.*\d)/;
      return !!this.passwordn && nume.test(this.passwordn);
    },
    raro(){
      let rar= /(?=.*[$@$!%*?& _])/;
      return !!this.passwordn && rar.test(this.passwordn);
    },
    completo(){
      return this.raro && this.numero && this.letras_m && this.letras_M && this.passwordn.length >= 8
    },
    iguales() {
      return !!this.passwordn && this.passwordn == this.passwordnc;
    },
    terminar() {
      return this.iguales && this.completo && ((!!this.password && this.password.length >=8)||!this.titulo);
    },
    ...mapState(['ip','No_Trabajador']),
  },

  watch: {},

  

  methods: {
    ...mapMutations(["Modificar_cargar","Salir","Modificar_Informacion",
    "Modificar_Espera",
    "Modificar_Correcto",
    "Modificar_Datos",
    "Modificar_Boton",
    "Modificar_ruta"]),
    async Asistencia_Docente(){
      this.Modificar_cargar(true);      
      this.Modificar_Informacion('Verificando Datos');
      this.Modificar_Espera(true);      
      const json = JSON.stringify({
        token:localStorage.getItem("Token"),
        pass:this.password,
        npass:this.passwordn
      });      
      
      
        let res = await axios.put("http://"+this.ip+"/api/docente/actualizar-pass", json, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "application/json",
          },
        }).catch(error =>{
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);          
          this.Modificar_Informacion("Error interno");  
          this.Modificar_Boton(true);
          console.log(error)        
        })
        
        if(!res.data.Error){          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);          
          this.Modificar_Informacion("Ingrese con su nueva contraseña");                              
          this.Salir();          
          this.Modificar_Boton(true);
          this.$router.push("/login");

          
        }
        else if(res.data.Error){          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);          
          this.Modificar_Informacion("No se puede cambiar la contraseña");                              
          this.Salir();           
          this.Modificar_Boton(true);
          this.$router.push("/login");          
        }
    }
  },
};
</script>
