import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    Nombre: "",
    No_Trabajador: "",
    login: false,
    ip: "104.215.80.110:3000",
    token: "",
    tipo_Usuario: null,
    No_JD: null,
    //Para el dialog
    carga_Activar: false,
    Informacion: "",
    Espera: "",
    Correcto: "",
    Datos: "",
    Boton: null,
    ruta: null,
  },
  mutations: {
    Salir(state) {
      if (typeof Storage !== "undefined") {
        localStorage.clear();
      }
      state.login = false;
      state.Nombre = "";
      state.No_Trabajador = "";
      state.token = "";
      state.tipo_Usuario = null;
      state.No_JD = null;
    },
    Actualizar_Nombre(state, nombre) {
      state.Nombre = nombre;
    },
    Actualizar_No_Trabajador(state, usuario) {
      state.No_Trabajador = usuario;
    },
    Actualizar_Tipo_User(state, usuario) {
      
      state.tipo_Usuario = usuario;
    },
    Actualizar_No_JD(state, usuario) {
      
      state.No_JD = usuario;
    },
    Modificar_login(state, n) {
      state.login = n;
    },
    // Checar_Token(state) {
    //   if (typeof Storage !== "undefined") {
    //     // Store
    //     if (localStorage.getItem("Token")) {
    //       state.login = true;
    //       console.log("Genial");
    //       return true;
    //     } else {
    //       return false;
    //     }
    //   } else {
    //     return false;
    //   }
    // },
    Actualizar_Token(state, dato) {
      state.token = dato;
    },
    Puedo_Iniciar(state) {
      state.Checar_Token();
    },
    //Para dialog
    Modificar_cargar(state, n) {
      state.carga_Activar = n;
    },
    Modificar_Informacion(state, n) {
      state.Informacion = n;
    },
    Modificar_Espera(state, n) {
      state.Espera = n;
    },
    Modificar_Correcto(state, n) {
      state.Correcto = n;
    },
    Modificar_Datos(state, n) {
      state.Datos = n;
    },
    Modificar_Boton(state, n) {
      state.Boton = n;
    },
    Modificar_ruta(state, n) {
      state.ruta = n;
    },
    Reiniciar_Valores(state) {
      state.carga_Activar = false;
      state.Informacion = "";
      state.Espera = "";
      state.Correcto = "";
      state.Datos = "";
      state.Boton = null;
      state.ruta = null;
    },
    async Checar_Token(state) {
      if (state.No_Trabajador == "") {
        
        if (localStorage.getItem("Token")) {
          
          
          const json = JSON.stringify({
            token: localStorage.getItem("Token"),
          });
          try {
            let res = await axios.post(
              "http://" + state.ip + "/api/docente/datos",
              json,
              {
                headers: {
                  // Overwrite Axios's automatically set Content-Type
                  "Content-Type": "application/json",
                },
              }
            ); 
            
                       
            if (!this.error && !res.data.Error) {
              state.No_Trabajador =await  res.data.Datos.No_Empleado;
              state.Nombre=await res.data.Datos.Nombre;
              state.login = true;
              state.tipo_Usuario =await res.data.Datos.User_T;
              state.No_JD = await res.data.Datos.No_Empleado;              
              
              return true
            } else if (res.data.Error) {              
              
              //localStorage.clear();
               state.carga_Activar = await false;
              state.Informacion = await  "";
              state.Espera = await  "";
              state.Correcto =  await  "";
              state.Datos =  await  "";
              state.Boton = await  null;
              state.ruta = await  null;
              
              return await false
            }
          } catch (error) {
            
            //localStorage.clear();
            state.carga_Activar = false;
            state.Informacion = "";
            state.Espera = "";
            state.Correcto = "";
            state.Datos = "";
            state.Boton = null;
            state.ruta = null;
            return false
          }
        }
      }else{
        return true
      }
    },
  },
  actions: {},
  modules: {},
});
