<script>
import { mapState, mapMutations} from "vuex";
import axios from "axios";
export default {
  mounted() {
    
    this.Ver();  
  },
  data() {
    return {      
      docente: false,
      admi: true,            
      drawer: false,
    };
  },
  name: "Barra",
  computed:{
      ...mapState(['login','Nombre','tipo_Usuario','ip']),
  },
  methods: {
    ...mapMutations(['Salir','Checar_Token']),
    Terminar(){
      this.drawer=false;
      this.Salir();
      this.$router.push("/login");    
      //console.log(this.$router);  
    },
    async Ver(){
      if (this.Nombre == "") {        
        if (localStorage.getItem("Token")) {
          
          
          const json = JSON.stringify({
            token: localStorage.getItem("Token"),
          });
          try {
            let res = await axios.post(
              "http://" + this.ip + "/api/docente/datos",
              json,
              {
                headers: {
                  // Overwrite Axios's automatically set Content-Type
                  "Content-Type": "application/json",
                },
              }
            ); 
            if (!res.data.Error) {
              this.Checar_Token();
            }            
                   
            else if (res.data.Error) {              
              this.Terminar()
            }
          } catch (error) {
            //console.log(error);
            //localStorage.clear();            
          }
        }
      }     
      
      
    }
  },
};
</script>
